@import url("https://fonts.cdnfonts.com/css/aniron");

@font-face {
  font-family: 'Avanti';
  src: url('./assets/fonts/AvantiBold.eot');
  src: url('./assets/fonts/AvantiBold.eot?#iefix') format('embedded-opentype'),
      url('./assets/fonts/AvantiBold.woff2') format('woff2'),
      url('./assets/fonts/AvantiBold.woff') format('woff'),
      url('./assets/fonts/AvantiBold.ttf') format('truetype');
  font-weight: bold;
  font-style: italic;
  font-display: swap;
}

* {
  box-sizing: border-box;
}

body {
  margin: 0;
  padding: 0;
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
